import { Fragment } from 'react'
import PropTypes from 'prop-types'
import NavDrawer, {
  DrawerHeader,
  DrawerSubtitle,
  DrawerTitle,
  DrawerContent,
} from '@material/react-drawer'
import DrawerList from './DrawerList'

const Contents = ({ setIsDrawerOpen }) => (
  <Fragment>
    <DrawerHeader>
      <DrawerTitle>
        Drawer Title
      </DrawerTitle>
      <DrawerSubtitle>
        Drawer Subtitle
      </DrawerSubtitle>
    </DrawerHeader>
    <DrawerContent>
      <DrawerList
        onItemClick={() => setIsDrawerOpen(false)}
      />
    </DrawerContent>
  </Fragment>
)
Contents.propTypes = {
  setIsDrawerOpen: PropTypes.func.isRequired,
}

const Drawer = ({ isOpen, setIsDrawerOpen }) => (
  <Fragment>
    <div
      className='app-drawer-container-mobile'
    >
      <NavDrawer
        modal={true}
        open={isOpen}
        onOpen={() => setIsDrawerOpen(true)}
        onClose={() => setIsDrawerOpen(false)}
      >
        <Contents
          setIsDrawerOpen={setIsDrawerOpen}
        />
      </NavDrawer>
    </div>
    <div
      className='app-drawer-container-desktop'
    >
      <NavDrawer>
        <Contents
          setIsDrawerOpen={setIsDrawerOpen}
        />
      </NavDrawer>
    </div>
    <style jsx>{`
      @media (min-width: ${breakpoint}px) {
        .app-drawer-container-mobile {
          display: none !important;
        }
      }
      @media (max-width: ${breakpoint-1}px) {
        .app-drawer-container-desktop {
          display: none !important;
        }
      }
    `}</style>
  </Fragment>
)
Drawer.propTypes = {
  breakpoint: PropTypes.number.isRequired,
  isOpen: PropTypes.bool.isRequired,
  setIsDrawerOpen: PropTypes.func.isRequired,
}

export default Drawer
